
'use client';

import React, { useState, useEffect, useCallback, useMemo } from 'react';
import { Zap, ArrowRight } from 'lucide-react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { FaHome, FaMagic, FaThLarge, FaHistory, FaRegCreditCard } from 'react-icons/fa';
import { motion, AnimatePresence } from 'framer-motion';
import { useTheme } from '@/context/ThemeContext';
import UserProfile from './UserProfile'; // Corrected import


interface SidebarProps {
  sidebarOpen: boolean;
  setSidebarOpen: (open: boolean) => void;
}

const Sidebar: React.FC<SidebarProps> = ({ sidebarOpen, setSidebarOpen }) => {
  const pathname = usePathname();
  const { theme } = useTheme();

  const navigation = useMemo(
    () => [
      { name: 'Dashboard', href: '/dashboard', icon: FaHome },
      { name: 'Generate', href: '/generate', icon: FaMagic },
      { name: 'Templates', href: '/templates', icon: FaThLarge },
      { name: 'History', href: '/history', icon: FaHistory },
      { name: 'Billing', href: '/billing', icon: FaRegCreditCard },
    ],
    []
  );

  const handleClose = useCallback(() => {
    setSidebarOpen(false);
  }, [setSidebarOpen]);

  return (
    <React.Fragment>
      <AnimatePresence>
        {sidebarOpen && (
          <motion.div
            className="relative z-40 md:hidden"
            role="dialog"
            aria-modal="true"
            initial={{ x: '-100%' }}
            animate={{ x: 0 }}
            exit={{ x: '-100%' }}
            transition={{ duration: 0.3, ease: 'easeInOut' }}
          >
            <motion.div
              className={`fixed inset-0 bg-ai-black bg-opacity-75 backdrop-blur-sm ${
                theme === 'dark' ? 'bg-opacity-75' : 'bg-opacity-50'
              }`}
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              onClick={handleClose}
            />
            <div className="fixed inset-0 flex z-40">
              <div className="relative flex-1 flex flex-col max-w-xs w-full bg-ai-black border-r border-ai-border shadow-2xl">
                <div className="absolute top-0 right-0 -mr-12 pt-2">
                  <button
                    type="button"
                    className="ml-1 flex items-center justify-center h-10 w-10 rounded-full bg-ai-surface border border-ai-border hover:bg-ai-gray hover:bg-opacity-20 focus:outline-none focus:ring-2 focus:ring-ai-green transition-all duration-200"
                    onClick={handleClose}
                  >
                    <span className="sr-only">Close sidebar</span>
                    <svg
                      className="h-6 w-6 text-ai-gray hover:text-white transition-colors"
                      xmlns="http://www.w3.org/2000/svg"
                      fill="none"
                      viewBox="0 0 24 24"
                      strokeWidth="1.5"
                      stroke="currentColor"
                      aria-hidden="true"
                    >
                      <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>
                <SidebarContent pathname={pathname} navigation={navigation} />
              </div>
              <div className="flex-shrink-0 w-14" aria-hidden="true" />
            </div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Static sidebar for desktop */}
      <div className="hidden md:flex md:w-64 md:flex-col md:fixed md:inset-y-0">
        <div className="flex-1 flex flex-col min-h-0 bg-ai-black border-r border-ai-border shadow-xl">
          <SidebarContent pathname={pathname} navigation={navigation} />
        </div>
      </div>
    </React.Fragment>
  );
};

interface SidebarContentProps {
  pathname: string;
  navigation: { name: string; href: string; icon: React.ComponentType }[];
}

const SidebarContent: React.FC<SidebarContentProps> = ({ pathname, navigation }) => {
  const { theme } = useTheme();
  
  return (
    <div className="flex-1 flex flex-col pt-6 pb-4 overflow-y-auto">
      <div className="flex items-center flex-shrink-0 px-4 mb-8">
        <Link
          href="/"
          className="text-2xl font-bold bg-gradient-to-r from-ai-green to-ai-blue bg-clip-text text-transparent hover:from-ai-blue hover:to-ai-green transition-all duration-300"
        >
          AI SEO Generator
        </Link>
      </div>
      <nav className="flex-1 px-3 space-y-2">
        {navigation.map((item) => {
          const isActive = pathname === item.href;
          return (
            <Link
              key={item.name}
              href={item.href}
              className={`group flex items-center px-3 py-3 text-sm font-medium rounded-xl transition-all duration-200 ${
                isActive
                  ? 'bg-gradient-to-r from-ai-green to-ai-blue text-white shadow-lg shadow-ai-green/20'
                  : `text-ai-gray hover:bg-ai-surface hover:text-white hover:shadow-md ${
                      theme === 'dark'
                        ? 'hover:bg-opacity-20'
                        : 'hover:bg-opacity-50'
                    }`
              }`}
              aria-current={isActive ? 'page' : undefined}
            >
              {React.createElement(item.icon, {
                className: `h-5 w-5 mr-3 transition-colors ${isActive ? 'text-white' : 'text-ai-gray group-hover:text-ai-green'}`,
                props: {
                  className: `h-5 w-5 mr-3 transition-colors ${isActive ? 'text-white' : 'text-ai-gray group-hover:text-ai-green'}`
                }
              })}
              {item.name}
            </Link>
          );
        })}
      </nav>
      <UserProfile />
    </div>
  );
};

export default Sidebar;
